import { useEthers } from "@usedapp/core";
import { AnimatePresence, motion } from "framer-motion";
import { useState } from "react";
import { FlexCol, FlexRow } from "../../components/layouts";
import { CenterizeMode } from "../../components/layouts/flex/types/FlexLayoutProps.types";
import TabBox from "../../components/tab-box/tab-box.component";
import { useNetwork } from "../../hooks";
import { AdvancedSwap } from "./AdvancedSwap/AdvancedSwap.feature";
import { DefaultSwap } from "./DefaultSwap/DefaultSwap.feature";

const tabItems = ["تبدیل ساده", "تبدیل پیشرفته"].reverse();

export function SwapBox() {
  const [activeTab, setActiveTab] = useState(1);
  const { account } = useEthers();
  const { changeNetwork, networkIndex, networkList } = useNetwork();

  const activateTab = async (tab: number) => {
    if (tab !== 1) {
      setActiveTab(tab);
      return;
    }

    if (networkIndex === 0) {
      setActiveTab(tab);
      return;
    }

    try {
      await changeNetwork(networkList[0]);
      setActiveTab(tab);
    } catch (e) {
      activateTab(tab);
    }
  };

  return (
    <FlexRow className="mx-auto w-full" centerize={CenterizeMode.both}>
      <FlexCol className="w-6/12 min-w-[400px] max-w-6xl">
        <TabBox
          tabs={tabItems}
          activeTab={activeTab}
          onActive={activateTab}
          className="p-5 transition-all"
        >
          <AnimatePresence initial={true} mode="wait">
            {activeTab === 1 ? (
              <motion.div
                key="default"
                initial={{ x: 20, opacity: 0 }}
                animate={{ x: 0, opacity: 1 }}
                exit={{ x: -20, opacity: 0 }}
              >
                <DefaultSwap account={account} />
              </motion.div>
            ) : (
              <motion.div
                key="advanced"
                initial={{ x: 20, opacity: 0 }}
                animate={{ x: 0, opacity: 1 }}
                exit={{ x: -20, opacity: 0 }}
              >
                <AdvancedSwap account={account} />
              </motion.div>
            )}
          </AnimatePresence>
        </TabBox>
      </FlexCol>
    </FlexRow>
  );
}
