import { IClickableElement } from "../../shared/interface/IClickableElement";
import { InteractableComponent } from "../../shared/types/InteractableComponent.type";

export function Button(props: InteractableComponent<IClickableElement>) {
  return (
    <button
      disabled={props.disabled}
      className={`py-2 px-5 text-white rounded-xl ${props.className || ""}`}
      onClick={props.onClick}
    >
      {props.children}
    </button>
  );
}
