import { CircularProgress } from "@mui/material";
import { useEtherBalance, useTokenBalance } from "@usedapp/core";
import { WalletIcon } from "../../assets/icons";
import DropDown from "../../components/drop-down/drop-down.component";
import { FlexCol, FlexRow } from "../../components/layouts";
import { Crypto } from "../../models/crypto";
import { CoinBoxProps } from "./types/CoinBoxProps.type";
import { formatEther, formatUnits } from "ethers/lib/utils";
import { useMemo } from "react";
import { formatPrice } from "../../tools/formatPrice";
import { useCoingeckoTokenPrice } from "@usedapp/coingecko";

export function CoinBox(props: CoinBoxProps) {
  const tokenPrice =
    useCoingeckoTokenPrice(props.token?.address || "", "USD") || "0";
  const index = useMemo(() => {
    return props.tokens?.findIndex(
      (item) => item.address === props.token?.address
    );
  }, [props.token?.address, props.tokens]);
  return (
    <FlexCol
      className={`w-full bg-white bg-opacity-50 p-5 gap-y-5 ${
        props.reverse
          ? "rounded-tl-3xl rounded-br-3xl"
          : "rounded-tr-3xl rounded-bl-3xl"
      }`}
    >
      <div className="grid grid-cols-10">
        <div className="col-span-3">
          {props.loadingTokens ? (
            <CircularProgress />
          ) : (
            <DropDown
              id="token"
              items={props.tokens?.map((i) => ({
                title: i.symbol,
                icon: `/coins/${i.symbol}.webp`,
              }))}
              title="توکن را انتخاب کنید"
              value={index}
              onSelect={(e) => {
                if (props.onTokenChange) {
                  props.onTokenChange(
                    props.tokens?.at(e) || new Crypto("", <></>, "", "")
                  );
                }
              }}
            />
          )}
        </div>
        <input
          type="number"
          placeholder="0.0"
          disabled={props.disabled}
          value={props.amount}
          onChange={(e) => {
            if (props.onAmountChange)
              props.onAmountChange(+e.currentTarget.value);
          }}
          className="w-full col-span-7 text-right outline-none bg-transparent text-theme-white text-3xl placeholder:text-white"
        />
      </div>
      <FlexRow className="justify-between items-center">
        <div className="flex items-center gap-x-2">
          <div>
            <WalletIcon />
          </div>
          <span className="text-2xl pt-1 text-theme-primary">
            {!props.token ? (
              <></>
            ) : !props.balance ? (
              <CircularProgress />
            ) : (
              formatPrice(formatEther(props.balance || formatUnits("0")))
            )}
          </span>
        </div>
        <div className="flex items-center text-theme-primary pb-0.5">
          <span className="text-2xl ">${tokenPrice}</span>
        </div>
      </FlexRow>
    </FlexCol>
  );
}
