import { motion, AnimatePresence } from "framer-motion";
import { ButtonBase } from "@mui/material";
import { useEtherBalance, useEthers } from "@usedapp/core";
import { getAddress } from "@zetachain/protocol-contracts";
import { BigNumber, ethers } from "ethers";
import { parseEther, parseUnits } from "ethers/lib/utils";
import { useMemo, useState } from "react";
import { ExchangeIcon } from "../../../assets/icons";
import { FlexCol } from "../../../components/layouts";
import { useNetwork } from "../../../hooks";
import { getSigner } from "../../../tools/getSigner";
import { NetworkedCoinBox } from "../../CoinBox/NetworkedCoinBox.feature";
import { ConnectWalletButton } from "../../ConnectWalletButton/ConnectWalletButton.feature";
import { AdvancedSwapProps } from "./types/AdvancedSwapProps.type";
import useZetaBalane from "../../../hooks/useZetaBalance";

const prepareParams = (types: string[], args: any) => {
  const abiCoder = ethers.utils.defaultAbiCoder;
  for (let i = 0; i < args.length; i++) {
    if (types[i] === "bytes32") {
      args[i] = ethers.utils.hexlify(ethers.utils.zeroPad(args[i], 32));
    }
  }
  return abiCoder.encode(types, args);
};

const prepareData = (contract: string, types: string[], args: any[]) => {
  const params = prepareParams(types, args);
  return `${contract}${params.slice(2)}`;
};

export function AdvancedSwap(props: AdvancedSwapProps) {
  const [switchState, setSwitchState] = useState(false);
  const [amount, setAmount] = useState(0);
  const {
    networkList,
    changeNetwork,
    networkIndex: network,
    loading: loadingNetworks,
  } = useNetwork();

  const { account } = useEthers();
  const { balance: zetaBalance } = useZetaBalane(
    account || "",
    networkList[network]
  );
  const netBalance =
    useEtherBalance(account, {
      chainId: networkList[network].chainId,
    }) || parseUnits("0");

  const insufficientValue = useMemo(() => {
    return (
      amount === 0 ||
      parseUnits(amount.toString()).gt(
        networkList[network].chainId === 7001 ? zetaBalance : netBalance
      )
    );
  }, [amount, netBalance, network, networkList, zetaBalance]);
  const filteredNetworks = useMemo(
    () =>
      networkList.filter(
        (item) => item.chainId !== networkList[network].chainId
      ),
    [network, networkList]
  );

  const [activeNet, setActiveNet] = useState(0);

  const otherNetBalance =
    useEtherBalance(account, {
      chainId: filteredNetworks[activeNet].chainId,
    }) || parseUnits("0");

  const onCrossChain = async () => {
    console.log(filteredNetworks[activeNet].zetaChainID);
    const targetZRC20 = getAddress(
      "zrc20",
      filteredNetworks[activeNet].zetaChainID
    );
    // filteredNetworks[activeNet].zetaChainID
    const to = getAddress("tss", networkList[network].zetaChainID);
    const value = parseEther(amount.toString());
    const minAmountOut = BigNumber.from("0");

    const data = !targetZRC20
      ? prepareData(
          "0xe585CeaD516B5AcE6c93FDf6F0c0a72cf5822DBa", // "0x5968A9512De05568c906286e230C669f05316C9A",
          ["bytes32"],
          [account]
        )
      : prepareData(
          "0xe585CeaD516B5AcE6c93FDf6F0c0a72cf5822DBa", // "0x5968A9512De05568c906286e230C669f05316C9A",
          ["address", "bytes32", "uint256"],
          [targetZRC20, account, minAmountOut]
        );

    const tx = await getSigner(account || "")?.sendTransaction({
      data,
      to,
      value,
    });

    await tx?.wait();
  };

  const onSwitch = async () => {
    try {
      const fromNetwork = networkList[network];
      const toNetwork = filteredNetworks[activeNet];
      await changeNetwork(toNetwork);
      setSwitchState(!switchState);
      const fromNetworkIndex = networkList
        .filter((item) => item.chainId !== toNetwork.chainId)
        .findIndex((i) => i.chainId === fromNetwork.chainId);

      console.log(networkList, fromNetworkIndex);
      setActiveNet(Math.max(fromNetworkIndex, 0));
    } catch (e) {
      console.log(e);
      return;
    }
  };

  return (
    <FlexCol>
      <div className="flex flex-col gap-y-4 relative">
        <NetworkedCoinBox
          networks={networkList}
          balance={
            networkList[network].chainId === 7001 ? zetaBalance : netBalance
          }
          loadingNetworks={loadingNetworks}
          activeNetwork={Math.max(network, 0)}
          onNetworkChange={(newNet) => changeNetwork(newNet)}
          onAmountChange={(e) => {
            setAmount(e);
          }}
        />
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2">
          <ButtonBase
            sx={{
              borderRadius: "50%",
              transition: "0.25s ease",
              rotate: switchState ? `180deg` : "0deg",
            }}
            onClick={onSwitch}
          >
            <div className="bg-theme-primary p-2.5 rounded-full border border-white w-12 h-12 flex justify-center items-center">
              <ExchangeIcon />
            </div>
          </ButtonBase>
        </div>
        <NetworkedCoinBox
          reverse
          disabled
          balance={
            filteredNetworks[activeNet].chainId === 7001
              ? zetaBalance
              : otherNetBalance
          }
          // balance={otherNetBalance}
          boxTitle="به"
          activeNetwork={activeNet}
          networks={filteredNetworks}
          loadingNetworks={loadingNetworks}
          onNetworkChange={(i) =>
            setActiveNet(
              filteredNetworks.findIndex((x) => x.chainId === i.chainId)
            )
          }
        />
      </div>
      <div className="py-2 flex justify-center items-center">
        {!props.account ? (
          <ConnectWalletButton className="bg-white w-full py-2.5 text-xl rounded-lg font-bold text-theme-primary" />
        ) : (
          <button
            disabled={insufficientValue}
            onClick={onCrossChain}
            dir="rtl"
            className="bg-white w-full py-2.5 text-xl
             rounded-lg disabled:bg-opacity-60 transition-all disabled:cursor-not-allowed font-bold text-theme-primary outline-none"
          >
            <AnimatePresence mode="wait" initial={true}>
              {insufficientValue ? (
                <motion.span
                  key={"insufficientValue"}
                  initial={{
                    x: 10,
                    opacity: 0,
                  }}
                  animate={{
                    x: 0,
                    opacity: 1,
                  }}
                  exit={{
                    x: -10,
                    opacity: 0,
                  }}
                >
                  {amount === 0
                    ? "مقدار را وارد کنید"
                    : "مقدار وارد شده بیشتر از مقدار موجود است"}
                </motion.span>
              ) : (
                <motion.span
                  key={"exchange"}
                  initial={{
                    x: 10,
                    opacity: 0,
                  }}
                  animate={{
                    x: 0,
                    opacity: 1,
                  }}
                  exit={{
                    x: -10,
                    opacity: 0,
                  }}
                >
                  تبدیل
                </motion.span>
              )}
            </AnimatePresence>
          </button>
        )}
      </div>
    </FlexCol>
  );
}
