import { CenterizeMode, FlexLayoutProps } from "./types/FlexLayoutProps.types";

const modesClassName = {
  [CenterizeMode.both]: "justify-center items-center",
  [CenterizeMode.horizontal]: "items-center",
  [CenterizeMode.vertical]: "justify-center",
  [CenterizeMode.none]: "",
};

export function FlexRow(props: FlexLayoutProps) {
  return (
    <div
      className={`flex ${props.reverse ? "flex-row-reverse" : "flex-row"} ${
        modesClassName[props.centerize || CenterizeMode.none]
      } ${props.className || ""}`}
    >
      {props.children}
    </div>
  );
}
