import { ButtonBase } from "@mui/material";
import {
  useContractFunction,
  useEtherBalance,
  useEthers,
  useTokenBalance,
} from "@usedapp/core";
import { ethers } from "ethers";
import { parseEther, parseUnits } from "ethers/lib/utils";
import { AnimatePresence, motion } from "framer-motion";
import { useMemo, useRef, useState } from "react";
import { from } from "rxjs";
import { ExchangeIcon } from "../../../assets/icons";
import { FlexCol } from "../../../components/layouts";
import { routerAbi } from "../../../contracts/abis";
import { useAmountOut, useApproval } from "../../../hooks";
import { useTokens } from "../../../hooks/useTokens";
import { ICryptoModel } from "../../../interfaces/ICrypto.model";
import { ROUTER_ADDR } from "../../../shared/config/dapp.confing";
import { emitAlert } from "../../../states/alert/alert.signal";
import { getSigner } from "../../../tools/getSigner";
import { CoinBox } from "../../CoinBox/CoinBox.feature";
import { ConnectWalletButton } from "../../ConnectWalletButton/ConnectWalletButton.feature";
import { SwapButtonState } from "../types/SwapButtons.enum";
import { swapButtonTexts } from "../types/constants/SwapButtonTexts";
import { DefaultSwapProps } from "./types/DefaultSwapProps.type";

export function DefaultSwap(props: DefaultSwapProps) {
  const [switchState, setSwitchState] = useState(false);
  const temp = useRef<any>();

  const [approving, setApproving] = useState<boolean>(false);
  const [swapping, setSwapping] = useState(false);

  const { account } = useEthers();
  const { loading, tokens } = useTokens();

  const [tokenA, setTokenA] = useState<ICryptoModel | undefined>();
  const tokenABalance = useTokenBalance(tokenA?.address, account);
  const [tokenAAmount, setTokenAAmount] = useState("0");

  const [tokenB, setTokenB] = useState<ICryptoModel | undefined>();
  const tokenBBalance = useTokenBalance(tokenB?.address, account);
  const tokeBAmount = useAmountOut(+tokenAAmount, tokenA, tokenB);

  const etherBalance = useEtherBalance(account);

  const { loading: availableTokenBLoading, tokens: availableTokenB } =
    useTokens(tokenA?.address);

  const { approve, isApproved } = useApproval(tokenA, tokenAAmount.toString());
  const swapContract = useContractFunction(
    new ethers.Contract(ROUTER_ADDR, routerAbi, getSigner(account || "")),
    "swapExactTokensForTokens",
    {
      transactionName: "swapExactTokensForTokens",
      gasLimitBufferPercentage: 10,
    }
  );

  const insufficientValue = useMemo(() => {
    if (!tokenABalance) return false;
    if (tokenAAmount === "0") return false;
    return parseUnits(tokenAAmount).gt(tokenABalance);
  }, [tokenAAmount, tokenABalance]);

  const approveToken = () => {
    setApproving(true);
    from(approve()).subscribe({
      complete: () => setApproving(false),
    });
  };
  const swap = async () => {
    setSwapping(true);
    from(
      swapContract.send(
        parseEther(tokenAAmount?.toString() || "0.0"),
        parseEther("0"),
        [tokenA?.address, tokenB?.address],
        account,
        Math.floor(Date.now() / 1000) + 60 * 20,
        {
          gasLimit: 6000000,
        }
      )
    ).subscribe({
      next: (v) => {
        if (!v)
          emitAlert({
            message: (
              <>
                {" "}
                درخواست تبدیل با خطا مواجه شد{" "}
                {/* <a href="https://goerli.etherscan.io/tx/">برای اطلاعات بیشتر اینجو کلیک کن</a> */}
              </>
            ),
            mode: "error",
          });
        else
          emitAlert({
            message: (
              <>
                {" "}
                تبدیل با موفقیت انجام شد{" "}
                <a
                  href={"https://goerli.etherscan.io/tx/" + v.transactionHash}
                  target="_blank"
                  className="text-blue-500"
                >
                  برای اطلاعات بیشتر اینجو کلیک کن
                </a>{" "}
              </>
            ),
            mode: "success",
          });

        console.log(v);
      },
      complete: () => {
        setSwapping(false);
      },
    });
  };

  return (
    <FlexCol>
      <div className="flex flex-col gap-y-4 relative">
        <CoinBox
          loadingTokens={loading}
          tokens={tokens}
          balance={tokenA?.symbol === "ETH" ? etherBalance : tokenABalance}
          onTokenChange={(token) => setTokenA(token)}
          token={tokenA}
          account={account}
          onAmountChange={(e) => {
            setTokenAAmount(e.toString());
          }}
          amount={tokenAAmount}
        />
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2">
          <ButtonBase
            sx={{
              borderRadius: "50%",
              transition: "0.25s ease",
              rotate: switchState ? `180deg` : "0deg",
            }}
            onClick={() => {
              setSwitchState(!switchState);
              temp.current = tokenA;
              setTokenA(tokenB);
              setTokenB(temp.current);
              setTokenAAmount(tokeBAmount.toString());
            }}
          >
            <div className="bg-theme-primary p-2.5 rounded-full border border-white w-12 h-12 flex justify-center items-center">
              <ExchangeIcon />
            </div>
          </ButtonBase>
        </div>
        <CoinBox
          reverse
          loadingTokens={availableTokenBLoading}
          tokens={availableTokenB}
          balance={tokenB?.symbol === "ETH" ? etherBalance : tokenBBalance}
          token={tokenB}
          onTokenChange={(token) => setTokenB(token)}
          account={account}
          amount={tokeBAmount.toString()}
          disabled
        />
      </div>
      <div className="py-2 flex justify-center items-center">
        {!props.account ? (
          <ConnectWalletButton className="bg-white w-full py-2.5 text-xl rounded-lg font-bold text-theme-primary" />
        ) : (
          <button
            disabled={
              swapping ||
              approving ||
              insufficientValue ||
              !tokenA ||
              !tokenB ||
              +tokenAAmount === 0
            }
            onClick={isApproved ? swap : approveToken}
            dir="rtl"
            className="bg-white w-full py-2.5 text-xl  rounded-lg disabled:bg-opacity-60 transition-all disabled:cursor-not-allowed font-bold text-theme-primary outline-none"
          >
            <AnimatePresence mode="wait" initial={true}>
              {!tokenA || !tokenB ? (
                <>لطفا توکن های خود را انتخاب کنید</>
              ) : isApproved ? (
                <motion.span
                  key={"swap"}
                  initial={{
                    x: 10,
                    opacity: 0,
                  }}
                  animate={{
                    x: 0,
                    opacity: 1,
                  }}
                  exit={{
                    x: -10,
                    opacity: 0,
                  }}
                >
                  {insufficientValue
                    ? swapButtonTexts[SwapButtonState.insufficientValue]
                    : +tokenAAmount === 0
                    ? "مقدار را وارد کنید"
                    : swapButtonTexts[
                        swapping
                          ? SwapButtonState.swapping
                          : SwapButtonState.swap
                      ]}
                </motion.span>
              ) : (
                <motion.span
                  key={"approve"}
                  initial={{
                    x: 10,
                    opacity: 0,
                  }}
                  animate={{
                    x: 0,
                    opacity: 1,
                  }}
                  exit={{
                    x: -10,
                    opacity: 0,
                  }}
                >
                  {insufficientValue
                    ? swapButtonTexts[SwapButtonState.insufficientValue]
                    : +tokenAAmount === 0
                    ? "مقدار را وارد کنید"
                    : swapButtonTexts[
                        approving
                          ? SwapButtonState.approving
                          : SwapButtonState.approve
                      ]}
                </motion.span>
              )}
            </AnimatePresence>
          </button>
        )}
      </div>
    </FlexCol>
  );
}
