import { BigNumber, ethers } from "ethers";
import { parseUnits } from "ethers/lib/utils";
import { useEffect, useState } from "react";
import { INetworkModel } from "../interfaces/INetwork.model";

async function getZetaBalance(account: string) {
  const RPC = "https://rpc.ankr.com/zetachain_evm_athens_testnet";
  const address = account;
  const provider = new ethers.providers.JsonRpcProvider(RPC);
  const balance = await provider.getBalance(address);
  return balance;
}

export default function useZetaBalane(account: string, net: INetworkModel) {
  const [balance, setBalance] = useState<BigNumber>(parseUnits("0"));

  useEffect(() => {
    if (!account) return;

    getZetaBalance(account).then((r) => {
      setBalance(r);
    });
  }, [account, net]);

  return {
    balance,
  };
}
