import { Button } from "../button/button.component";
import { FlexCol } from "../layouts/flex/flex-col.component";
import { FlexRow } from "../layouts/flex/flex-row.component";
import { TabBoxProps } from "./types/TabBoxProps.type";

export function TabBox(props: TabBoxProps) {
  const emitActivate = (index: number) => () => {
    if (props.onActive) props.onActive(index);
  };

  return (
    <FlexCol className="w-full">
      <FlexRow className="justify-start border-b-white border-b" reverse>
        <FlexRow className="py-5 gap-x-5">
          {props.tabs?.map((item, index) => (
            <Button
              key={`${index}-${Date.now()}-tabBoxItemInstance`}
              onClick={emitActivate(index)}
              className={`border ${
                props.activeTab === index
                  ? "bg-theme-blue border-white transition-all"
                  : "bg-transparent border-transparent transition-all"
              } transition-all`}
            >
              {item}
            </Button>
          ))}
        </FlexRow>
      </FlexRow>
      <FlexCol className={props.className || ""}>{props.children}</FlexCol>
    </FlexCol>
  );
}
export default TabBox;
